<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col sm:w-1/2">
        <p class="info-row">
          <span>{{ $t('CARD_NUMBER') }}:</span>
          <span>{{ card.cardNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('NAME_ON_CARD') }}:</span>
          <span>{{ card.embossedName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CARD_TYPE') }}:</span>
          <span>{{ card.typeName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CARD_STATUS') }}:</span>
          <span>{{ card.statusName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ISSUANCE_DATE') }}:</span>
          <span>{{ card.issuanceDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('EXPIRY_DATE') }}:</span>
          <span>{{ card.expiryDate | formatUnixDate }}</span>
        </p>
      </div>
      <div class="nv-col sm:w-1/2">
        <p class="info-row">
          <span>{{ $t('CREDIT_LIMIT') }}:</span>
          <span :class="negativeNumberStyle(card.creditLimit)">{{ card.creditLimit | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('AVAILABLE_CREDIT') }}:</span>
          <span :class="negativeNumberStyle(card.availableCreditAmount)">{{ card.availableCreditAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TOTAL_DUE_AMOUNT') }}:</span>
          <span :class="positiveNumberStyle(card.totalDueAmount)">{{ card.totalDueAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MINIMUM_DUE_AMOUNT') }}:</span>
          <span :class="positiveNumberStyle(card.minimumDueAmount)">{{ card.minimumDueAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="card.paymentDueDate">
          <span>{{ $t('PAYMENT_DUE_DATE') }}:</span>
          <span>{{ card.paymentDueDate | formatUnixDate }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import CreditCardService from '@/services/card/CardService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'

export default {
  name: 'credit-card-view',
  mixins: [NumberStyleMixin, LoadingMixin, AlertMixin],
  data: function () {
    return {
      card: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      CreditCardService.creditCards({
        cardId: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.card = response.data.cardList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
