import Endpoint from '@/services/Endpoint'

export default {
  creditCards (payload) {
    return Endpoint().get('/s1/connect/card/credit', {
      params: payload
    })
  },
  cardTransactions (payload) {
    return Endpoint().get(`/s1/connect/card/credit/${payload.cardId}/transaction`, {
      params: payload
    })
  },
  officialStatements (payload) {
    return Endpoint().get(`/s1/connect/card/credit/${payload.cardId}/statement/official`, {
      params: payload
    })
  },
  modifyPin (payload) {
    return Endpoint().patch('/s1/connect/card/credit/pin', payload)
  },
  creditCardStatus (payload) {
    return Endpoint().get('/s1/connect/card/credit/status', {
      params: payload
    })
  }
}
